header {
    padding: 20px;
    margin-bottom: 20px;

}

header .logo-header {
    font-family: 'PilseungGothic', sans-serif;
    font-size: 80px;
    font-style: italic;
    color: white;
    text-align: center;
    text-shadow: 0 0 7px #37ffe4;
}

header .logo-header span {
    display: inline-block;
    animation: jump .5s alternate-reverse infinite;
}

header .logo-header>strong {
    font-size: 150px;
}

header .logo-header span:nth-child(1) {
    animation-delay: .3s;
}

header .logo-header span:nth-child(2) {
    animation-delay: .4s;
}

header .logo-header span:nth-child(3) {
    animation-delay: .5s;
}

header .logo-header span:nth-child(4) {
    animation-delay: .6s;
}

header .logo-header span:nth-child(5) {
    animation-delay: .7s;
}

@keyframes jump {
    from {
        transform: translateY(5%);
    }

    to {
        transform: translateY(-10%);
    }
}

@media screen and (width <=650px) {
    header {
        padding: 20px;
        margin-bottom: 20px;

    }

    header .logo-header {
        font-size: 60px;
    }

    header .logo-header>strong {
        font-size: 110px;
    }
}