.input-wrap-outline {
    position: relative;
    width: 606px;
    height: 606px;
    margin: 0 auto;
    background-color: #002420;
    box-sizing: border-box;
    border-radius: 50px;
    overflow: hidden;
}

.input-wrap-outline::before {
    content: "";
    position: absolute;
    top: 50%;
    left: -175px;
    width: 450px;
    height: 450px;
    background: linear-gradient(120deg, #37ffe4, rgba(55, 255, 228, 0) 60%);
    transform-origin: right top;
    animation: rotation 60s linear infinite;
    animation-delay: -3s;
    z-index: 0;
}

.input-wrap-outline::after {
    content: "";
    position: absolute;
    top: 50%;
    left: -175px;
    width: 450px;
    height: 200px;
    background: linear-gradient(140deg, #ffe735, rgba(255, 231, 53, 0) 60%);
    transform-origin: right top;
    animation: rotation 1s linear infinite;
    animation-delay: -3s;
    z-index: 1;
}

.input-wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    top: 3px;
    left: 3px;
    padding: 20px;
    width: 600px;
    height: 600px;
    box-sizing: border-box;
    border-radius: 50px;
    background-color: #464646;
    font-family: 'PilseungGothic', sans-serif;
    font-size: 40px;
    color: white;
    text-align: center;
    z-index: 2;
}

.input-wrap input {
    all: unset;
    height: 80px;
    margin: 20px;
    box-sizing: border-box;
    padding-bottom: 5px;
    border-bottom: 3px solid white;
    font-size: 60px;
    color: #00ddbf;
    vertical-align: middle;
}

.promise-btn {
    all: unset;
    position: relative;
    width: 300px;
    height: 60px;
    margin-top: 20px;
    border-radius: 10px;
    background-color: #333;
    box-shadow: 0 0 5px rgba(255, 255, 255, .5);
    cursor: pointer;
}

.promise-btn:disabled {
    box-shadow: none;
}

.promise-btn:not(:disabled):hover {
    box-shadow: 0 0 10px#37ffe4;
    animation: shadow .7s linear alternate-reverse infinite;
}

#expert_user {
    width: 350px;
}

#time_user {
    width: 100px;
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@keyframes shadow {
    from {
        box-shadow: 0 0 20px #37ffe4;
    }

    to {
        box-shadow: 0 0 5px #37ffe4;
    }

}



@media screen and (width <=650px) {
    .input-wrap-outline {
        width: 100%;
        overflow: hidden;
        border-radius: 0;
    }

    .input-wrap-outline::before {
        display: none;
    }

    .input-wrap-outline::after {
        display: none;
    }

    .input-wrap {
        width: 100%;
        border-radius: 0;
    }
}