.time-disp {
    all: unset;
    position: absolute;
    top: 20px;
    left: 20px;
    font-family: 'PilseungGothic', sans-serif;
    font-size: 30px;
    color: white;
    text-align: center;
}

@media screen and (width <=1000px) {
    .time-disp {
        left: -40px;
        font-size: 25px;
    }
}

@media screen and (width <=650px) {
    .time-disp {
        display: none;
    }
}