.output-cover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .5);

    z-index: 5;
}

.output-wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 800px;
    height: 800px;
    background-color: #333;
    border-radius: 50%;
    font-family: 'PilseungGothic', sans-serif;
    font-size: 50px;
    color: white;
    text-align: center;
    transform: translate(-50%, -50%);
}

.output-wrap::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 800px;
    height: 800px;
    border-radius: 50%;
    box-shadow: 10px 10px 10px rgba(55, 255, 228, .5);
    animation: rotation 5s linear infinite;
}

.view_expert,
.view_period {
    font-size: 100px;
    color: #37ffe4;
}

.close-btn {
    all: unset;
    position: relative;
    width: 300px;
    height: 60px;
    margin-top: 50px;
    border-radius: 10px;
    background-color: #464646;
    box-shadow: 0 0 5px rgba(255, 255, 255, .5);
    cursor: pointer;
}

.close-btn:hover {
    box-shadow: 0 0 10px#37ffe4;
    animation: shadow .7s linear alternate-reverse infinite;
}

@media screen and (width <=750px) {
    .output-wrap {
        top: 50%;
        left: 50%;
        width: 600px;
        height: 600px;
        font-size: 30px;
    }

    .output-wrap::before {
        width: 600px;
        height: 600px;
    }

    .view_expert,
    .view_period {
        font-size: 60px;
    }

    .close-btn {
        width: 250px;
    }
}

@media screen and (width <=650px) {
    .output-wrap {
        top: 50%;
        left: 50%;
        width: 100%;
        border-radius: 0;
    }

    .output-wrap::before {
        display: none;
    }
}